import * as React from "react";

function App() {
    const [counter, setCounter] = React.useState(60);
  
    React.useEffect(() => {
      const timer =
        counter > 0 && setInterval(() => setCounter(counter - 1), 1000);
      return () => clearInterval(timer);
    }, [counter]);
  
    console.log(counter)
    return (
        counter
    );
  }

  export default App;
